html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Rubik", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  position: relative;
  background: linear-gradient(188deg, #957fdf 5%, #915de4 35%, #522fa5 100%);
  max-height: 90rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 15rem;
}
.header .svg {
  display: flex;
  align-self: flex-end;
  padding: 2rem 2rem 1rem 0;
  justify-content: flex-end;
}

.borde {
  border: 1px solid #e1e1e1;
  margin: 0 12rem;
  padding: 1rem;
  border-radius: 50%;
}
.borde .contenedor-circulo {
  display: flex;
  justify-content: center;
}
.borde .contenedor-circulo .icono-usuario {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  background-image: url(../../../build/img/definicion-de-persona-min.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.informe-cuenta {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.informe-cuenta .nombre {
  font-weight: 500;
  font-size: 2.5rem;
  margin: 0;
  color: #FFFFFF;
}
.informe-cuenta .tiempo {
  margin-top: -2rem;
  color: rgba(225, 225, 225, 0.5);
  font-weight: 300;
}

.boton {
  background-color: #000000;
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5rem;
  width: 60%;
  text-align: center;
  border: none;
}

.fondo-blanco {
  position: absolute;
  z-index: 2000;
  background: #e1e1e1;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  width: 100%;
  margin: -5rem auto 0 auto;
}
@media (min-width: 768px) {
  .fondo-blanco {
    display: flex;
    justify-content: center;
    height: 100vh;
  }
}

.contenedor-cartas {
  margin: 3rem 2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.contenedor-cartas .carta {
  background: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 2rem;
  border-radius: 1rem;
}
.contenedor-cartas .carta:hover {
  transform: translateY(-5px) scale(1.005) translateZ(0);
  box-shadow: 0 24px 36px rgba(0, 0, 0, 0.11), 0 24px 46px #e1e1e1;
}
.contenedor-cartas .shopping .s {
  background: #C6F4ED;
}
.contenedor-cartas .shopping:hover {
  transition: 0.5s ease-out;
  background: #C6F4ED;
}
.contenedor-cartas .shopping:hover .s {
  transition: 0.5s ease-out;
  background: #A7D3D3;
}
.contenedor-cartas .groceries .g {
  background: #F8BFBF;
}
.contenedor-cartas .groceries:hover {
  transition: 0.5s ease-out;
  background: #F8BFBF;
}
.contenedor-cartas .groceries:hover .g {
  transition: 0.5s ease-out;
  background: #d18e8e;
}
.contenedor-cartas .stats .st {
  background: #F1EBF6;
}
.contenedor-cartas .stats:hover {
  transition: 0.5s ease-out;
  background: #F1EBF6;
}
.contenedor-cartas .stats:hover .st {
  transition: 0.5s ease-out;
  background: #B99EE5;
}
.contenedor-cartas .ubers .u {
  background: #F1DBFF;
}
.contenedor-cartas .ubers:hover {
  transition: 0.5s ease-out;
  background: #F1DBFF;
}
.contenedor-cartas .ubers:hover .u {
  transition: 0.5s ease-out;
  background: #AD83DF;
}
.contenedor-cartas .dates .d {
  background-color: #FFEFCC;
}
.contenedor-cartas .dates:hover {
  transition: 0.5s ease-out;
  background: #FFEFCC;
}
.contenedor-cartas .dates:hover .d {
  transition: 0.5s ease-out;
  background: #E5C58F;
}
.contenedor-cartas .cards .c {
  background-color: #D7F7FF;
}
.contenedor-cartas .cards:hover {
  transition: 0.5s ease-out;
  background: #D7F7FF;
}
.contenedor-cartas .cards:hover .c {
  transition: 0.5s ease-out;
  background: #81CEE1;
}
@media (min-width: 768px) {
  .contenedor-cartas {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .contenedor-cartas .carta {
    padding: 10rem 2rem;
    width: 15rem;
  }
  .contenedor-cartas .stats {
    margin: -8rem 0 auto;
  }
  .contenedor-cartas .ubers, .contenedor-cartas .dates, .contenedor-cartas .cards {
    margin: -14rem 0 auto;
  }
}
@media (min-width: 1024px) {
  .contenedor-cartas .ubers, .contenedor-cartas .dates, .contenedor-cartas .cards {
    margin: -44rem 0 auto;
  }
}

.top {
  margin: -8rem 0 auto;
}

.contenedor-svg {
  margin-top: 2rem;
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.texto-titulo {
  font-weight: 500;
}